'use client'
import styles from "./page.module.css";
import {useState} from "react"
import {SacoUi} from "./ui/SacoUi"

export default function Home() {

  const [products, setProducts] = useState([
    {nombre: "whiskas", peso: 10, precioPublico: 1000 },
    {nombre: "pedigree", peso: 15, precioPublico: 1200 },
    {nombre: "dog chow", peso: 20, precioPublico: 1500 }
  ]);

  const [selectedProducts, setSelectedProducts] = useState(null);
  const [isSelectedProducts, setIsSelectedProducts] = useState(false);


  const handleAddToSelected = (product) => {
      setSelectedProducts(prevSelected => {
          const currentCount = prevSelected[product.nombre] || 0;
          return {
              ...prevSelected,
              [product.nombre]: currentCount + 1
          };
      });
  };

  return (
    <div style={{
      width:"100vw",
      height:"100vh",
      display:"flex",
      flexDirection:"column",
      padding:"1rem"
    }}>
      <div style={{flexGrow:1, overflowY:"auto", padding: "0 1rem"}}>
        {isSelectedProducts ?
          <ListSelectedProducts  selectedProducts={selectedProducts} />
        :
          <ListProducts 
            products={products} 
            Card={SacoUi} 
            onAdd={handleAddToSelected} 
            selectedProducts={selectedProducts}
          />
        }
      </div>
      <div style={{padding:"1rem"}}>
       <FormSearchProducts />
      </div>
    </div>
  );
}

const FormSearchProducts = () => {
  const formStyle = {
    display: 'flex',
    gap: '0.5rem',
  };
  const inputStyle = {
    flexGrow: 1,
    padding: '0.5rem',
    borderRadius: '4px',
    border: '1px solid #ccc'
  };
  const buttonStyle = {
    padding: '0.5rem 1rem',
    borderRadius: '4px',
    border: 'none',
    backgroundColor: '#0070f3',
    color: 'white',
    cursor: 'pointer'
  }
  return(
    <form style={formStyle}>
      <button type="button" style={{...buttonStyle, backgroundColor: '#6c757d'}}>
        Limpiar
      </button>
      <input type="text" style={inputStyle} placeholder="Buscar producto..."/>
      <button type="submit" style={buttonStyle}>Buscar</button>
    </form>
  )
}

const ButtonAdd = ({ onClick, count }) => {
    const buttonStyle = {
        backgroundColor: '#28a745',
        color: 'white',
        border: 'none',
        borderRadius: '5px',
        padding: '10px 15px',
        cursor: 'pointer',
        fontSize: '1rem',
        height: 'fit-content',
        display: 'flex',
        alignItems: 'center',
        gap: '8px'
    };

    const countStyle = {
      backgroundColor: 'white',
      color: '#28a745',
      borderRadius: '50%',
      padding: '2px 8px',
      fontWeight: 'bold',
      fontSize: '0.9rem'
    }

    return (
        <button style={buttonStyle} onClick={onClick}>
            Agregar
            {count > 0 && <span style={countStyle}>{count}</span>}
        </button>
    );
}

const ListProducts = ({products, Card, onAdd, selectedProducts}) => {
    const listContainerStyle = {
      display: 'flex',
      flexDirection: 'column',
      gap: '1rem'
    }
    const productItemStyle = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        gap: '1rem'
    };

    return(
      <div style={listContainerStyle}>
        {products?.map((product) => (
          <div key={product.nombre} style={productItemStyle}>
            <div style={{flexGrow: 1}}>
              <Card product={product} />
            </div>
            <ButtonAdd 
              onClick={() => onAdd(product)} 
              count={selectedProducts[product.nombre] || 0}
            />
          </div>
        ))}
      </div>
    )
}

const ListSelectedProducts = ({selectedProducts}) => {
  return(
    <div style={listContainerStyle}>
        {selectedProducts?.map((product) => (
          <div key={product.nombre} style={productItemStyle}>
            <div style={{flexGrow: 1}}>
              <Card product={product} />
            </div>
            <ButtonAdd 
              onClick={() => onAdd(product)} 
              count={selectedProducts[product.nombre] || 0}
            />
          </div>
        ))}
      </div>
  )
}
